<template>
	<div id="search">
		<div class="topctrl">
		  <div class="back" @click="$router.go(-1)"><i class="fa fa-angle-left fa-2x"></i></div>
		  <input class='searchInput' v-model="searchWorld" placeholder="搜索歌曲、歌手" type="text">
		  <i class="el-icon-close back" @click="searchWorld=''" v-if="searchWorld!=''"></i>
		</div>
		<div class="resultsBox">
			<div class="HS-title">热门搜索</div>
			<div class="hotSearchBox" >
				<div class="cellHS" v-for="(item,index) of hotList" :key="index" @click="searchWorld=item.first">{{item.first}}</div>
				<div class="SearchResults" v-for="(item,index) of searchList" :key="index">
					<div class="searchName">{{item.name}}</div>
					<div>{{item.artists[0].name}}</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>

const debounce = (func, wait) => {
  let timeout = '';
  return (v) => {
    if (timeout) {
      clearTimeout(timeout);
    }
    timeout = setTimeout(() => {
      func(v);
    }, wait);
  }
};

export default {
    mounted (){
    	this.geteSearchHot();
    },
    data () {
    	return {
    		hotList:[],
    		searchWorld:'',
    		searchList:[],
    	}
    },
    watch: {
	    searchWorld: function () {
	    	this.debounceSearch(this);
	    }
	},
    methods:{
    	geteSearchHot:function(){
				var that =this
				this.$axios.get('http://120.79.162.149:3000/search/hot')
				.then(re =>{
						that.hotList=re.data.result.hots
				})
    	},
    	debounceSearch:debounce(function(e)  {
		    if(e.searchWorld!=''){
		    	e.getHot();
		    }else{
		    	e.searchList=[]
		    }
		},400),
    	getHot: function () {
				var that =this
				this.$axios.get('http://120.79.162.149:3000/search?keywords='+ this.searchWorld)
				.then(re =>{
						that.searchList=re.data.result.songs
				})
	    },
	    
    }
}

</script>

<style scoped>
#search{position: fixed;top: 0;background: #fff;}
.topctrl{background: #d44439;display: flex;position: fixed;top: 0;width: 100%;}
.resultsBox{margin-top: 44px;}
.searchInput {background: transparent;border: none;border-bottom: 1px solid #dddad3;color: #fff;border-radius: 0;width: calc(100% - 88px);height: 28px;margin: auto 0;}
.searchInput:focus{outline: none;}
.HS-title{text-align: left;padding: 10px 0 8px 14px;font-size: 12px;}
.hotSearchBox{display: flex;flex-wrap: wrap;padding: 0 10px;}
.cellHS{border: 1px solid #f3f3f3;border-radius: 50px;margin: 4px;font-size: 12px;padding: 3px 10px;background: #f3f3f3;}
.SearchResults{border-bottom: 1px solid #e4e4e4;width: 100%;text-align: left;height: 40px;font-size: 10px;line-height: 20px;}
.searchName{width: 100%;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;}
</style>


