<template>
  <div id="menu">
    <div class="menuMask"  @click="closeMask()"></div>
    <div class="leftMenu" >
       <!-- 4343432    v-if="mask"   v-if="Menu" -->
        <div class="" v-if="login">
            <p>登入后手机电脑多端同步，尽享海量高品质音乐</p>
            <el-button @click="geto('loging')" size="mini" round>登入</el-button>
        </div>
        <div  v-if="!login">
          <div class="us-head" v-bind:style="{backgroundImage:'url('+ userInfo.backgroundUrl +')'}">
            <img class="avatar" :src="userInfo.avatarUrl" alt="">
            <div class="us-ctrl">
              <div class="us-name">{{userInfo.nickname}}</div>
              <div>{{userInfo.name}}</div>
              <el-button  size="mini" round>签到</el-button>
            </div>
          </div>
          <div>
            <div class="cellMenu" v-for="(item,index) of menuList" :key="index">
              <i class="fa" v-bind:class="[item.icon]"></i>
              {{item.name}}
              </div>
          </div>
          <div class="set-ctrl">
            <div class="cellMenu cl6" ><i class="fa fa-moon-o"></i>夜间模式</div>
            <div class="cellMenu cl3" ><i class="fa fa-cog"></i>设置</div>
            <div class="cellMenu cl3" @click="Exit()"><i class="fa fa-power-off"></i>退出</div>
          </div>
        </div>
    </div>
  </div>
</template>

<script >
export default {
  mounted (){
      this.getData()
    },
  data () {
    return {
      Menu:false,
      // mask:false,
      userInfo:[],
      login:'',
      menuList:[
        {name:'我的消息',icon:'fa-envelope-open-o'},
        {name:'会员中心',icon:'fa-android'},
        {name:'村村有票',icon:'fa-ticket'},
        {name:'商城'    ,icon:'fa-shopping-cart'},
        {name:'游戏'    ,icon:'fa-gamepad'},
        {name:'我的好友',icon:'fa-user-o'},
        {name:'皮肤商城',icon:'fa-tint'},
        {name:'听歌识曲',icon:'fa-microphone'},
        ],
      
    }
},
  methods:{
    closeMask:function(){
      // this.Menu=false
      // this.mask=false
      this.$router.go(-1);
    },
    getData(){
      this.userInfo = JSON.parse(localStorage.getItem('userInfo'));
      this.userInfo==null?this.login=true:this.login=false
    },
    Exit(){
      localStorage.clear()
      this.$router.go({path:'hoom1'})
    }
  }
}
</script>
<style>
.menuMask{width: 100vw;height: 100vh;background: #000000;opacity: .4;position: fixed;top: 0;left: 0;z-index: 555;}
.leftMenu{position: fixed;top: 0;left: 0;width: 84vw;height: 100vh;background: #f2f3f4;z-index: 600;}
.us-head{width: 100%;height: 162px;background-size: cover;background-position: 0 37%;color: #fff;display: flex;flex-wrap: wrap}
.avatar{width: 80px;height: 80px;border-radius: 50px;margin: 40px 170px 10px 10px}
.us-name{font-size: 16px;}
.us-ctrl{display: flex;width: 100%;align-items: center;justify-content: space-between;padding: 0 10px;}
.cellMenu{text-align: left;background: #fff;padding: 10px ;}
.cellMenu i{width: 20px;text-align: center;}
.set-ctrl{position: absolute;bottom: 0;left: 0;width: 100%;display: flex;}
.set-ctrl .cellMenu+.cellMenu{text-align: right;}
</style>


