// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import './assets/font-awesome-4.7.0/css/font-awesome.min.css'
// import './assets/jquery-3.3.1.min.js'
import './assets/Mtils.min.js'
import VueAwesomeSwiper from 'vue-awesome-swiper'


// require styles
import 'swiper/dist/css/swiper.css'

Vue.use(VueAwesomeSwiper, /* { default global options } */)

import axios from 'axios'
Vue.prototype.$axios = axios

Vue.use(ElementUI)

Vue.config.productionTip = false


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  
})
// 公共函数
Vue.prototype.toggleTab = function (e){
  sessionStorage.setItem('homeID',e)
  this.homeID = e;
  this.$router.push({ path: 'home'+e }) ;
}
Vue.prototype.geto =function(e){
  this.$router.push({ path: e }) ;
  if(e=='menu'){
    this.Menu==true? this.Menu=false:this.Menu=true
    this.mask==true? this.mask=false:this.mask=true
  }
}

