<template>
  <div>
    <div class="homeTabbar">
      <div class="m-head">
        <div class="m-h-btn" @click="geto('menu')">
          <i class="fa fa-bars"></i>
        </div>
        <div class="m-c-btn">
          <div @click="topChange('me')" class="m-h-btn" :class="[add1=='me'?'add1Active':'']" id="2"><i class="fa fa-music"></i></div>
          <div @click="topChange('home1')" class="m-h-btn" :class="[add1=='home1'?'add1Active':'']" id="1"><i class="fa fa-star"></i></div>
          <div @click="topChange('vid')" class="m-h-btn" :class="[add1==3?'add1Active':'']" id="3"><i class="fa fa-video-camera"></i></div>
        </div>
        <div class="m-h-btn" @click="geto('search')">
          <i class="fa fa-search"></i>
        </div>
      </div>
      
    </div>
  </div>
</template>

<script>
export default {
  mounted() {},
  data() {
    return {
        add1:'',
        
    };
  },
  created(){
    // sessionStorage.getItem('homeID')==null?this.homeID=1:this.homeID=sessionStorage.getItem('homeID')
    sessionStorage.getItem('add1')==undefined?this.homeID='home1':this.add1=sessionStorage.getItem('add1')
  },
  methods: {
    topChange:function(e){
      sessionStorage.setItem('add1',e)
      this.add1=e
      this.$router.push({path:e})
    },
  }
};
</script>
<style>
.homeTabbar{background: #e14439;border-bottom: 1px solid #e14439;}
.c-hui{color:#a7a7a7;}
.m-head{width: 100vw;height: 44px;display: flex;align-items: center;justify-content: space-between;}
.m-name{line-height: 44px;font-weight: 700;color: #f1f1f1;letter-spacing: 3px;line-height: 44px;}
.m-h-btn{width: 44px;height: 44px;border: none;background: transparent;color: rgba(255, 255, 255, 0.68);;font-size: 20px;line-height: 44px;border-radius: 50px;}
.m-h-btn:active{background:rgba(0, 0, 0, 0.15);}
.m-c-btn{display: flex;}
.add1Active{color: #fff;}
</style>


