<template>
	<div id="">
		<div class="m-body">
	  	<div id="" v-if="HotArtistsList!=''">
	  		<ul class="allBox">
				<li class="cellAlphabet" >
					<h2>热门</h2>
					<ul>
						<li class="cellArtidsts"  v-for="(item,index) of HotArtistsList" :key="index" @click="jumpArtists(item.id)">
							<img :src="item.img1v1Url" alt="">
							<span>{{item.name}}</span>
						</li>
					</ul>
				</li>
				<li class="cellAlphabet" v-for="(item,index) of artistsList" :key="index">
					<h2>{{item.name}}</h2>
					<ul>
						<li class="cellArtidsts" v-for="(item,index) of item.event" :key="index">
							<img :src="item.img1v1Url" alt="">
							<span>{{item.name}}</span>
						</li>
					</ul>
				</li>
			</ul>
			<ul class="shouzimu">
				<li>热</li>
				<li v-for="(item,index) of artistsList" :key="index">{{item.name}}</li>
			</ul>
	  	</div>
			
	  </div>
	</div>
</template>

<script>
export default {
	mounted(){
		this.getData();
	},
	data () {
		return {
				HotArtistsList:[],
				artistsList:[],
		}
	},
	methods:{
		getData: function(e){
			var that = this
			this.$axios.get('http://120.79.162.149:3000/top/artists?limit=100')
			.then(re => {
				that.HotArtistsList=re.data.artists.slice(0,10)
				that.artistsList=re.data.artists
				var Alist  = re.data.artists
				for (const i in Alist) {
					var SZM = Mtils.utils.makePy(Alist[i].name.slice(0,1), true);
					var szm = {'SZM':SZM}
					Object.assign(Alist[i],szm)
				}
				var resultArray = Alist.sort(
						function compareFunction(param1, param2) {
								return param1.SZM.localeCompare(param2.SZM,"zh");
						}
				);
				var lingshiA={name:'A',event:[]},endA=[]
				lingshiA.event.push(resultArray[0])
				for (var j in resultArray) {
					var a1 = resultArray[j].SZM 
					var x = parseInt(j)+1
					if(parseInt(j) == resultArray.length-1){
						--x
					}
					var a2 = resultArray[x].SZM
					if(a1 == a2){
						lingshiA.event.push(resultArray[x])
					}else{
						endA.push(lingshiA)
						lingshiA = {name:'',event:[]}
						lingshiA.event.push(resultArray[x])
						lingshiA.name=a2
					}
				}
				endA.push(lingshiA)
				that.artistsList=endA
			})
		},
		jumpArtists:function(e){
			this.$router.push({ path: 'artists' ,query:{id:e}}) ;
		}
	}
}
</script>

<style scoped>
.shouzimu{position: fixed;right: 0;top: 88px;}
.shouzimu li{padding: 5px;line-height: 1;color: #757575;font-size: 11px;font-weight: 700;}
.allBox{width: 100vw;height: calc(100vh - 88px);overflow: auto;}
.cellAlphabet h2{width: 100%;height: 20px;background: #d9dadb;margin: 0;font-size: 12px;line-height: 20px;text-align: left;color: #fff;padding-left: 12px;}

.cellArtidsts{width: 100%;height: 60px;border-bottom: 1px solid #e4e4e4;display: flex;align-items: center;}
.cellArtidsts img{width: 50px;height: 50px;border-radius: 3px;margin: 0 12px;}
</style>


